body {
  background-image: linear-gradient(to bottom right, #DECBA4, #508787);
}

.data-vis-wrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.data-vis-wrapper .recharts-responsive-container {
  min-width: 100%;
  display: flex;
}
.recharts-surface {
  overflow: visible;
}

@media only screen and (min-width: 992px) {
  .data-vis-wrapper {
    flex-direction: row;
  }
  .data-vis-wrapper .recharts-responsive-container {
    flex-basis: 50%;
    max-width: 50%;
    min-width: auto;
    margin: 0 auto;
  }
}

.App {
   padding: 3% 10%;

}

.App, h1 {
   text-align:center
}

.App * {
   box-sizing:border-box
}

li {
   padding:1%
}

ul {
   padding: 0;
   list-style:none
}

.description {
    margin-left: auto;
    margin-right: auto;
    width: 45em
    
}

input[type=number], input[type=text] {
   padding: 8px 6px 8px 10px;
   border-radius: 4px;
   outline: none;
   border: 1px solid #ccc;
   font-size: 14px;
   line-height: 20px;
   align-self: center;
   height:38px
}

.city {
   width:300px
}

.event {
   padding: 10px 10px 35px;
   margin-bottom: 10px;
   text-align: center;
   border: 1px solid #7a7a7a;
   border-radius: 4px;
   position:relative;
   background-color: rgba(42, 31, 26, 0.5);
   color: #fff;
}

button {
   background-color: #cab5b5;
   font-weight: 700;
   color:#000
}

.WelcomeScreen {
   position: fixed;
   width: 100%;
   height: 100%;
   top: 0;
   background:#fff
}

.login-button {
   color: #494949;
   text-transform: uppercase;
   text-decoration: none;
   background: #fff;
   border: 4px solid #494949;
   display: inline-block;
   transition: all .4s ease 0s;
   min-width: 10rem;
   margin:1rem
}

.google-btn {
   width: 184px;
   height: 42px;
   margin: 1rem;
   background-color: #4285f4;
   border-radius: 2px;
   box-shadow:0 3px 4px 0 rgba(0, 0, 0, .25)
}

.google-btn:hover {
   box-shadow:0 0 6px #4285f4
}

.google-btn:active {
   background:#1669f2
}

.google-icon-wrapper {
   position: absolute;
   margin-top: 1px;
   margin-left: 1px;
   width: 40px;
   height: 40px;
   border-radius: 2px;
   background-color:#fff
}

.google-icon {
   position: absolute;
   margin-top: 11px;
   margin-left: -8px;
   width: 18px;
   height:18px
}

.btn-text {
   float: right;
   color: #fff;
   font-size: 14px;
   letter-spacing: .2px;
   font-family: "Roboto";
   background-color: #4285f4;
   width: 80%;
   height: 100%;
   text-align:center
}

#nprogress {
   pointer-events:none
}

#nprogress .bar {
   background: #29d;
   position: fixed;
   z-index: 1031;
   top: 0;
   left: 0;
   width: 100%;
   height:2px
}

#nprogress .peg {
   display: block;
   position: absolute;
   right: 0;
   width: 100px;
   height: 100%;
   box-shadow: 0 0 10px #29d, 0 0 5px #29d;
   opacity: 1;
   -webkit-transform: rotate(3deg) translateY(-4px);
   transform:rotate(3deg) translateY(-4px)
}

#nprogress .spinner {
   display: block;
   position: fixed;
   z-index: 1031;
   top: 15px;
   right:15px
}

#nprogress .spinner-icon {
   width: 18px;
   height: 18px;
   box-sizing: border-box;
   border-color: #29d transparent transparent #29d;
   border-style: solid;
   border-width: 2px;
   border-radius: 50%;
   -webkit-animation: nprogress-spinner .4s linear infinite;
   animation:nprogress-spinner .4s linear infinite
}

.nprogress-custom-parent {
   overflow: hidden;
   position:relative
}

.nprogress-custom-parent #nprogress .bar, .nprogress-custom-parent #nprogress .spinner {
   position:absolute
}

@-webkit-keyframes nprogress-spinner {
   0% {
       -webkit-transform:rotate(0deg)
   }

   to {
       -webkit-transform:rotate(1turn)
   }
}

@keyframes nprogress-spinner {
   0% {
       -webkit-transform: rotate(0deg);
       transform:rotate(0deg)
   }

   to {
       -webkit-transform: rotate(1turn);
       transform: rotate(1turn)
   }
}
